body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {}

.header {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.header h1 {
  color: black;
  font-size: 4vh;
}

.mainContent {
  height: 80vh;

}

.homeLink {
  text-decoration: none;
  color: salmon;
}

h1 {
  color: salmon;
  font-size: 7vh;
}

h2 {
  color: black;
  font-size: 4vh;
}

h6 {
  text-align: center;
  font-size: 20px;

}

.App {
  height: 50vh;
  text-align: left;

}

.logo {
  justify-content: left;
  width: 40%;
  border-radius: 10px;
  box-shadow: 0px 0px 5px 1px;
}


.topnav {
  overflow: hidden;
  background-image: url(/static/media/pexels-simone-defendi-12762122.c31e7e9c.jpg);
  background-repeat: round;
  position: -webkit-sticky;
  position: sticky;
}

.topnav #myLinks {
  display: none;
  text-align: center;
}

.topnav a {
  color: white;
  padding: 14px 16px;
  text-decoration: none;
  font-size: 17px;
  display: block;
}

.topnav a.icon {
  display: block;
  position: absolute;
  right: 0;
  top: 0;
  margin-top: 20px;
}

.topnav a:hover {
  color: white;
}


.active {
  color: white;
}

.fa {
  font-size: 25px;
}


/* SHOPPING PAGE */

.shopifyHdr {
  display: flex;
  justify-content: space-around;
  padding: 10px;
  background-color: gray;
}





